@import "../../mixins";
.Footer {
  grid-area: footer;
  background-color: #f5f5f5;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  @include rm(601) {
    padding: 0 10px; }
  .default-btn {
    margin: 0;
    @include r(600) {
      height: 35px;
      font: 13px/36px Gotham Pro Med, sans-serif; } }
  &-col {
    font-family: 'Gotham Pro Med', sans-serif;
    font-size: 14px;
    white-space: nowrap;
    &-title {
      font-size: 13px;
      color: #ababab;
      margin-bottom: 10px;
      font-weight: 400;
      font-family: 'Gotham Pro';
      @include r(600) {
        margin-bottom: 5px; } } }
  &-back {
    cursor: pointer;
    @include nosel;
    width: 32px;
    height: 32px;
    padding: 4px;
    color: #cbc9c9;
    transition: color .3s;
    &-disabled {
      opacity: .6;
      pointer-events: none; }
    &:hover {
      @include notouch {
        color: #ba9984; } }
    &:active {
      transform: rotate(-360deg);
      color: #ba9984;
      transition: color .15s,transform .3s; }
    svg {
      width: 100%;
      height: 100%; } } }
