/*stylelint-disable*/
$units: 1px !default;
@mixin screen($res-min, $res-max) {
	@media screen and ( min-width: if(unitless($res-min), $res-min*$units, $res-min) ) and ( max-width: if(unitless($res-max), $res-max*$units, $res-max) ) {
		@content; } }
// less than X
@mixin r($width) {
	@media only screen and (max-width: $width+ "px") {
		@content; } }

// more than X
@mixin rm($width) {
	@media only screen and (min-width: $width+ "px") {
		@content; } }

// height, less than X
@mixin rh($height) {
	@media only screen and (max-height: $height + "px") {
		@content; } }
@mixin css-lock($min-size: 1, $max-size: 1.4, $min-width: 20, $max-width: 100, $property: font-size, $unit: px) {
	#{$property}: #{$max-size}#{$unit};
	#{$property}: calc(#{$min-size}#{$unit} + (#{$max-size} - #{$min-size}) * ((100vw - #{$min-width}#{$unit}) / (#{$max-width} - #{$min-width})));
	@media (max-width: #{$min-width}#{$unit}) {
		#{$property}: #{$min-size}#{$unit}; }
	@media (min-width: #{$max-width}#{$unit}) {
		#{$property}: #{$max-size}#{$unit}; } }
@mixin ie11 {
	@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
		@content; } }
@mixin notouch {
	@media not all and (-moz-touch-enabled:1) , not all and (pointer:coarse) , not all and (-ms-high-contrast:none) and (max-width:600px) {
		@content; } }
@mixin clr {
	&:after {
		content: " ";
		display: table;
		clear: both; }
	*zoom: 1; }
@mixin cut($line) {
	display: -webkit-box;
	text-overflow: ellipsis;
	-webkit-line-clamp: $line;
	-webkit-box-orient: vertical;
	overflow-wrap: break-word;
	overflow: hidden; }
@mixin padlr($val) {
	@include rem(padding-left,$val);
	@include rem(padding-right,$val); }

@mixin g($color1, $color2) {
	background-color: $color1;
	background-image: linear-gradient($color1, $color2); }

@mixin gh($color1, $color2) {
	background-color: $color1;
	background-image: linear-gradient(to right, $color1, $color2); }

// easy vertical-align at center
@mixin vcenter {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%); }

// bulletproof vertical-align
@mixin vertical {
	text-align: center;
	font-size: 0;
	&:before {
		content: "";
		display: inline-block;
		vertical-align: middle;
		width: 0;
		height: 100%; } }

// justify, set on parent
@mixin justify {
	text-align: justify;
	line-height: 0;
	font-size: 0;
	text-justify: newspaper;
	zoom: 1;
	text-align-last: justify;
	&:after {
		content: "";
		display: inline-block;
		width: 100%;
		height: 0px;
		visibility: hidden;
		overflow: hidden; } }

// fast font-family
@mixin font($alias, $name) {
	@font-face {
		font-family: $alias;
		src: url("../fonts/" + $name + ".eot");
		src: url("../fonts/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + ".woff") format("woff"), url("../fonts/" + $name + ".ttf") format("truetype");
		font-weight: normal;
		font-style: normal; } }
// usage:
//+font(black, cytiapro-black-webfont)

@mixin placeholder {
	&::placeholder {
		@content; }
	&::-moz-placeholder {
		@content; }
	&::-ms-input-placeholder {
		@content; } }

@mixin text-overflow {
	overflow: hidden;
	-ms-text-overflow: ellipsis;
	text-overflow: ellipsis;
	white-space: nowrap; }

@mixin ie {
	*display: inline;
	*zoom: 1; }

@mixin box {
	box-sizing: border-box; }

%pseudo {
	position: relative;
	&:before,&:after {
		content: ' ';
		position: absolute;
		top: 0;
		left: 0; } }
@mixin pseudo {
	@extend %pseudo; }
@mixin boxshadow {
	box-shadow: 0 -2px 0 0 transparent,0 1px 0 0 currentcolor; }
@mixin coverdiv {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%; }

@mixin cover {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%; }
@mixin contain {
	background-repeat: no-repeat;
	background-size: contain;
	background-position: 50% 50%; }
@mixin bg-size($width: 100%, $height: 100%) {
	background-size: $width $height; }

// retina, also available as +hdpi
@mixin retina {
	@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
		@content; } }

@mixin tr($what) {
	transition: $what; }

@mixin hide-text {
	font: 0/0 a;
	text-shadow: none;
	color: transparent; }

@mixin hidetext {
	@include hide-text; }

@mixin unselectable {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none; }

@mixin nosel {
	@include unselectable; }

@mixin prevent-text {
	-ms-word-break: break-all;
	word-break: break-all;
	word-break: break-word;
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	hyphens: auto; }

@mixin font-smoothing($val: antialiased) {
	-webkit-font-smoothing: $val;
	-moz-font-smoothing: $val;
	-ms-font-smoothing: $val;
	-o-font-smoothing: $val; }

@mixin selection {
	& ::selection {
		@content; }
	& ::-moz-selection {
		@content; } }
@mixin sel {
	@include selection; }

@mixin gpu {
	transform: translate3d(0, 0, 0); }

@mixin smooth-triangle {
	transform: rotate(0.05deg); }

@mixin scrollbar {
	&::-webkit-scrollbar {
		@content; } }

@mixin scrollbar-track {
	&::-webkit-scrollbar-track {
		@content; } }

@mixin scrollbar-thumb {
	&::-webkit-scrollbar-thumb {
		@content; } }

@mixin scrollbar-track-piece {
	&::-webkit-scrollbar-track-piece {
		@content; } }

//triangle
@mixin arr($width, $height, $bg, $direction) {
	width: 0;
	height: 0;
	border-style: solid;
	@if $direction == t {
		border-width: 0 $width / 2 + px $height + px $width / 2 + px;
		border-color: transparent transparent $bg transparent; }
	@if $direction == r {
		border-width: $height / 2 + px 0 $height / 2 + px $width + px;
		border-color: transparent transparent transparent $bg; }
	@if $direction == b {
		border-width: $height + px $width / 2 + px 0 $width / 2 + px;
		border-color: $bg transparent transparent transparent; }
	@if $direction == l {
		border-width: $height / 2 + px $width + px $height / 2 + px 0;
		border-color: transparent $bg transparent transparent; }
	@if $direction == tl {
		border-width: $height + px $width + px 0 0;
		border-color: $bg transparent transparent transparent; }
	@if $direction == tr {
		border-width: 0 $width + px $height + px 0;
		border-color: transparent $bg transparent transparent; }
	@if $direction == br {
		border-width: 0 0 $height + px $width + px;
		border-color: transparent transparent $bg transparent; }
	@if $direction == bl {
		border-width: $height + px 0 0 $width + px;
		border-color: transparent transparent transparent $bg; } }
// example
// +arr(500,50, #000, l)
// +arr(500,50, #000, bl)

@mixin rotate($var) {
	transform: rotate($var+deg); }

@mixin blur($var) {
	-webkit-filter: blur($var);
	filter: blur($var); }

@mixin scale($var) {
	transform: scale($var); }

@mixin counter($var, $sep) {
	counter-reset: list + $var;
	> li {
		&:before {
			content: counter(list + $var) $sep;
			counter-increment: list + $var; } } }

@function makelongshadow($color) {
	$val: 0px 0px $color;
	@for $i from 1 through 200 {
		$val: #{$val}, #{$i}px #{$i}px #{$color}; }
	@return $val; }

@mixin longshadow($color) {
	text-shadow: makelongshadow($color); }

@mixin fs($sizeValue: 16) {
	font-size: $sizeValue + px;
	font-size: $sizeValue / 10 + rem; }


@mixin rem($property, $values) {
	$base-font-size: 16px;
	$px-values: ();
	$rem-values: ();

	@each $value in $values {
		@if $value == 0 or $value == 0px {
			$px-values: join($px-values, 0);
			$rem-values: join($rem-values, 0); }
		@else if type-of($value) == number and not unitless($value) and (unit($value) == px) {
			$new-rem-value: $value / $base-font-size;
			$px-values: join($px-values, round($value));
			$rem-values: join($rem-values, #{$new-rem-value}rem); }
		@else if $value == auto or $value == inherit {
			$px-values: join($px-values, $value);
			$rem-values: join($rem-values, $value); }
		@else {
			$px-values: join($px-values, round($value * $base-font-size));
			$rem-values: join($rem-values, #{$value}rem); } }

	// #{$property}: $px-values
	#{$property}: $rem-values; }

$dottedLineSpacing: 5;
@mixin dotted-line($color: #000, $alignment: bottom) {
	background-image: linear-gradient(to right, $color 0%, $color (100% / $dottedLineSpacing), transparent (100% / $dottedLineSpacing));
	background-position: 0 $alignment; background-size: ($dottedLineSpacing + px) 2px;
	background-repeat: repeat-x; }

@mixin fadeText($text-color, $direction : top, $offset : 2em, $transition: 0.5s ease-out) {
	// text-color : dark
	$color: #fff;

	// direction : top
	$deg: 180deg;
	$pos: bottom;

	@if($text-color == 'light') {
		$color: #000;
		mix-blend-mode: screen; }
	@else {
		mix-blend-mode: multiply; }

	@if($direction == 'left') {
		$deg: 90deg;
		$pos: right; }
	@elseif ($direction == 'right') {
		$deg: -90deg;
		$pos: left; }
	@elseif ($direction == 'bottom') {
		$deg: 0deg;
		$pos: top; }

	position: relative;

	&::after {
		background: linear-gradient($deg, transparent, $color $offset) no-repeat $pos center / 100% 100%;

		content: '';
		position: absolute;
		top: -30px;
		right: 0px;
		bottom: 0px;
		left: 0px;
		pointer-events: none;
		@include tr( $transition); }
	&.active::after {
		@if($direction == 'left' or $direction == 'right') {
			background-size: 0% 100%; }
		@else {
			background-size: 100% 0%; } } }
