@import "../../mixins";
.Cart {
  grid-area: cart;
  @include rm(1021) {
      padding-right: 30px; }
  &-title {
    font-size: 13px;
    color: #000;
    font-family: Gotham Pro Med, sans-serif;
    span {
      color: #c1bebe; } }
  &-scroll {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    overflow-x: auto;
    overflow-y: hidden;
    margin-top: 12px;
    width: 100%;
    @include r(600) {
      margin-top: 6px; } }
  &-item {
    width: 40px;
    height: 50px;
    position: relative;
    margin-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain; } }
  &-delete {
    position: absolute;
    right: 0;
    top: 0;
    border-radius: 100%;
    color: white;
    width: 20px;
    height: 20px;
    background: #c1bebe;
    padding: 5px;
    font-size: 0;
    cursor: pointer;
    @include tr(background .2s);
    &:hover {
      @include notouch {
        background: #ba9984; } }

    svg {
      width: 100%;
      height: 100%; } } }

