@import "../../mixins";
.Filter {
  position: absolute;
  right: 0;
  top: 0;
  background: white;
  height: 100%;
  z-index: 3;
  display: flex;
  flex-flow: column nowrap;
  transition: transform .15s ease-in-out, opacity .15s, visibility .15s;
  box-shadow: 0 0 5px rgba(0, 0, 0, .15);
  width: 100%;
  max-width: 76%;
  padding-left: 30px;
  will-change: transform;
  contain: paint;
  @include r(600) {
    padding-left: 15px;
    padding-top: 15px;
    padding-right: 15px;
    position: fixed; }

  &:not(.Filter-open) {
    transform: translateX(100%);
    opacity: 0;
    visibility: hidden; }
  &-section {
    margin-top: 35px;
    &-title {
      text-transform: uppercase;
      font-size: 14px;
      color: black;
      font-family: 'Gotham Pro Med', Arial,serif;
      margin-bottom: 15px;
      display: block; }
    &-option {
      font-size: 13px;
      cursor: pointer;
      transition: color .3s;
      padding-top: 5px;
      padding-bottom: 5px;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      @include nosel;
      svg {
        opacity: 0;
        @include tr(opacity .2s); }
      &.active {
        color: #ba9984;
        svg {
          opacity: 1; } }
      &:hover {
        &:not(.active) {
          text-decoration: underline; } } }
    &-icon {
      opacity: 1 !important;
      width: 20px;
      height: 20px;
      margin: -2px auto -2px 15px; } }

  &-options {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center; }
  &-content {
    flex: 1;
    overflow-y: auto;
    padding-bottom: 20px; } }

.Filter-btn {
  background: 0 0;
  border: none;
  cursor: pointer;
  outline: none;
  font-size: 12px;
  text-transform: uppercase;
  padding-bottom: 5px;
  padding-left: 0;
  padding-right: 0;
  @include tr(color .3s);
  @include nosel;
  &:hover {
    @include notouch {
      color: #ba9984; } }
  &:after, span {
    content: "";
    color: #9d9d9d; }
  &:after {
    margin-left: 4px;
    letter-spacing: 1px; } }

.Filter-price- {
  &:after {
    content: "\2191\2193";
    letter-spacing: -0.5px;
    vertical-align: baseline; } }
.Filter-price-min {
  &:after {
    content: "\2191"; } }
.Filter-price-max {
  &:after {
    content: "\2193"; } }

.Filter-clear {
  text-transform: none;
  color: #c1bebe; }


