@import "mixins";

html,body {
  width: 100%;
  height: 100%; }
body {
  display: flex;
  flex-flow: column nowrap;
  overscroll-behavior: none;
  .page-head {
    .dropdown.top-city {
      max-width: 70px !important; } }
  &>.content-wrap {
    flex: 1;
    width: 100%;
    display: flex;
    flex-flow: column nowrap; } }

#constructor {
  width: 100%;
  display: grid;
  grid-gap: 17px;
  grid-template-columns:  minmax(0px, 1fr) 36.5%;
  grid-template-areas: "ear left" "ear left" "cart footer";
  grid-template-rows: auto auto 110px;
  position: relative;
  flex: 1;
  @include r(1020) {
    grid-template-areas: "ear ear" "left left" "cart cart" "footer footer";
    grid-template-rows: auto 1fr auto 60px;
    overflow: hidden; }
  @include r(600) {
    grid-gap: 5px;
    grid-template-rows: auto 1fr auto 46px;
    min-height: 500px; }
  * {
    box-sizing: border-box; } }
.constructor-back {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin: 20px 0;
  text-align: left;
  flex-shrink: 0;
  i {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    border: 1px solid #e5e5e5;
    width: 37px;
    height: 37px;
    margin-right: 15px;
    flex-shrink: 0;
    padding: 5px; }
  @include r(600) {
    font-size: 17px;
    margin: 15px 0;
    i {
      width: 20px;
      height: 20px; } }
  @include r(440) {
    font-size: 14px;
    margin: 5px 0;
    i {
      width: 15px;
      height: 15px; } } }

