@import "../../mixins";
.Ear {
  grid-area: ear;
  display: flex;
  flex-flow: column nowrap;
  overflow: hidden;
  position: relative;
  @include rm(1021) {
    padding-right: 30px; }
  @media screen and (max-width: 767px), screen and (max-device-width: 767px) {
    margin: 0 -10px; }
  &~div[data-rbd-placeholder-context-id] {
    display: none !important; }
  &-img {
    max-width: 100%;
    flex-grow: 1;
    object-fit: cover;
    pointer-events: none;
    align-self: flex-start; }
  &-pin {
    position: absolute;
    width: 50px;
    height: 50px;
    z-index: 2;
    &.pin-0, &.pin-11 {
      top: 77.15%;
      left: 43.6%; }
    &.pin-1, &.pin-12 {
      top: 73.5%;
      left: 48.2%; }
    &.pin-2, &.pin-13 {
      top: 68.1%;
      left: 52.12%; }
    &.pin-3, &.pin-14 {
      top: 62.11%;
      left: 55.75%; }
    &.pin-4, &.pin-15 {
      top: 55.15%;
      left: 58.33%; }
    &.pin-5, &.pin-16 {
      top: 37.26%;
      left: 60.58%; }
    &.pin-6, &.pin-17 {
      top: 30%;
      left: 59.6%; }
    &.pin-7, &.pin-18 {
      top: 26.05%;
      left: 53.35%; }
    &.pin-8, &.pin-19 {
      top: 21.5%;
      left: 43.8%; }
    &.pin-9, &.pin-20 {
      top: 28%;
      left: 40.3%; }
    &.pin-10, &.pin-21 {
      top: 55.6%;
      left: 38.9%; }
    &.pin-11, &.pin-12, &.pin-13, &.pin-14, &.pin-15, &.pin-16, &.pin-17, &.pin-18, &.pin-19, &.pin-20, &.pin-21 {
      left: auto; }
    &.pin-11 {
      right: 43.6%; }
    &.pin-12 {
      right: 48.2%; }
    &.pin-13 {
      right: 52.12%; }
    &.pin-14 {
      right: 55.75%; }
    &.pin-15 {
      right: 58.33%; }
    &.pin-16 {
      right: 60.58%; }
    &.pin-17 {
      right: 59.6%; }
    &.pin-18 {
      right: 53.35%; }
    &.pin-19 {
      right: 43.8%; }
    &.pin-20 {
      right: 40.3%; }
    &.pin-21 {
      right: 38.9%; } }

  &-wrapper {
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    max-height: 100%;
    @include nosel;
    &-left {
      .Ear-img {
        transform: scaleX(-1); } }
    &:not(.Ear-wrapper-active) {
      display: none; }
    @include r(1020) {
      max-width: 500px;
      margin: -7% auto -4%; } }
  &-buttons {
    position: absolute;
    right: 45px;
    top: 15px;
    display: flex;
    flex-flow: row nowrap;
    z-index: 1;
    border: 1px solid white;
    @include r(1020) {
      right: calc(50% - 235px); }
    @include r(520) {
      right: 20px;
      top: 10px; } }
  &-button {
    width: 40px;
    height: 40px;
    color: white;
    cursor: pointer;
    @include nosel;
    &:first-child {
      transform: scale(-1,1);
      border-left: 1px solid white; }
    &-active {
      background: white;
      color: #B49B82; } } }
