@import "../../mixins";
.cardList {
  grid-area: left;
  display:  flex;
  flex-flow: column nowrap;
  position: relative;
  overflow: hidden;
  @include r(1020) {
    position: static; }
  &-inner {
    display:  flex;
    flex-flow: column nowrap;
    align-self: stretch;
    width: 100%;
    position: relative;
    flex-grow: 1; }
  &-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    grid-gap: 10px;
    overflow-y: auto;
    overflow-x: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 8px 10px 8px 8px;
    align-items: flex-start;
    grid-auto-rows: minmax(min-content, max-content);
    @include r(600) {
      grid-template-columns: repeat(auto-fill, minmax(70px, 1fr)); }
    &::-webkit-scrollbar {
      width: 6px;
      border-radius: 6px; }
    &::-webkit-scrollbar-thumb {
      background: #e9e9e9;
      border-radius: 10px; } }
  &-filters {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 7px;
    @include r(600) {
      padding-bottom: 0; } } }

