@import "../../mixins";

.Getter {
  .placeholder-wrapper {
    opacity: 0;
    visibility: hidden; }
  @include nosel;
  * {
    outline: none; }
  &-image {
    width: 100%; }
  &-container {
    position: absolute;
    top: -50%;
    left: -50%;
    bottom: -50%;
    right: -50%;
    pointer-events: none; }

  &-drag {
    position: absolute;
    width: 50%;
    height: 50%;
    left: 25%;
    top: 25%;
    z-index: 2;
    pointer-events: all;
    contain: layout; }
  &-content {
    position: relative;
    width: 100%;
    height: 100%;
    &:after {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      border-radius: 200%;
      z-index: -2;
      width: 7px;
      height: 7px;
      background: white;
      transform: translate(-50%, -50%);
      transition: transform .1s ease-in-out, opacity .1s;
      transition-duration: 0.1s;
      overflow: hidden; }
    &.Getter-has-item {
      &:after {
        opacity: 0;
        transition-duration: 0s; } }
    &.Getter-active {
      &:after {
        transform: translate(-50%, -50%) scale(3);
        transition-duration: 0.2s; }
      &.Getter-over {
        .Getter-image {
          transform: translateY(100%); }
        &:after {
          transform: translate(-50%, -50%) scale(6);
          opacity: .7;
          transition-duration: 0.15s; } }
      &.Getter-has-item {
        &:after {
          transform: translate(-50%, -50%) scale(6);
          opacity: .5; }
        &.Getter-over {
          &:after {
            transform: translate(-50%, -50%) scale(8);
            opacity: .9; } }
