@import "../../mixins";

.Card {
  cursor: grab;
  position: relative;
  z-index: 1;
  background: white;
  flex-grow: 0;
  //transform: none !important
  &-hidden {
    display: none; }
  &-cloned {
    &~ {
      transform: none; } }
  &:only-child {
    max-width: 125px; }
  @include nosel;
  * {
    box-sizing: border-box; }
  &:hover {
    @include notouch {
      z-index: 2;
      filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, .15));
      transition: filter .2s;
      .Card-info:not(.Card-info-dragging) {
        opacity: 1;
        pointer-events: all;
        transition: opacity .1s, visibility .1s; }
      .Card-plus {
        opacity: 1; } } }
  &>img {
    @include coverdiv;
    z-index: 1;
    object-fit: contain;
    object-position: center;
    pointer-events: none; }
  &:after {
    content: "";
    display: block;
    width: 100%;
    padding-bottom: 70%; }
  &-plus {
    position: absolute;
    background: #de7c67;
    border-radius: 100%;
    z-index: 3;
    width: 30px;
    height: 30px;
    padding: 8px;
    color: white;
    left: 50%;
    top: 50%;
    margin-left: -15px;
    margin-top: -15px;
    @include tr(opacity .2s);
    pointer-events: none;
    opacity: 0;
    svg {
      width: 100%;
      height: 100%; } }
  &-info {
    position: absolute;
    z-index: 2;
    top: 100%;
    left: 0;
    width: 100%;
    background: white;
    opacity: 0;
    pointer-events: none;
    text-align: center;
    @include padlr(10px);
    padding-bottom: 20px;
    .product-card-title, .product-card-price {
      margin-top: 0;
      padding: 0;
      width: 100%; }
    .product-card-title {
      font-size: 11px;
      line-height: 1.1;
      //max-height: 25px
      margin-bottom: 7px;
      @include cut(2); }
    .product-card-price {
      font-size: 13px; } }
  .product-card-price.dragging-visible {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 100%;
    background: white;
    font-size: 12px;
    white-space: nowrap;
    padding: 5px;
    border-radius: 10px;
    opacity: 0;
    visibility: hidden;
    &.Card-dragging {
      opacity: 1;
      visibility: visible;
      transition: visibility .2s, opacity .2s;
      box-shadow: 0 0 4px 0 rgba(0,0,0,0.15); } } }


